.side-menu a {
    width: 100%;
    display: block;
    padding: 8px 20px;
    text-decoration: none;
    font-family: 'Montserrat', sans-serif;
    font-family: 'Raleway', sans-serif;
}

.side-menu i {
    font-size: 20px;
    vertical-align: middle;
}

.side-menu:first-child i {
    color: #008073 !important;
    font-size: 22px;
    margin-right: 5px;
}

.active {
    font-weight: 600;
    letter-spacing: 1px;
}

.side-menu:first-child a:hover {
    background: #f2f2f2;
    border-left: 5px solid #008073;
}

.side-menu:first-child .active {
    border-left: 5px solid #008073;
}

.side-menu:nth-child(2) i {
    color: #00bcd4 !important;
}

.side-menu:nth-child(2) a:hover {
    background: #f2f2f2;
    border-left: 5px solid #00bcd4;
}

.side-menu:nth-child(2) .active {
    border-left: 5px solid #00bcd4;
}

.side-menu:nth-child(3) i {
    color: #3f51b5 !important;
}

.side-menu:nth-child(3) a:hover {
    background: #f2f2f2;
    border-left: 5px solid #3f51b5;
}

.side-menu:nth-child(3) .active {
    border-left: 5px solid #3f51b5;
}

.side-menu:nth-child(4) i {
    color: #9c27b0 !important;
}

.side-menu:nth-child(4) a:hover {
    background: #f2f2f2;
    border-left: 5px solid #9c27b0;
}

.side-menu:nth-child(4) .active {
    border-left: 5px solid #9c27b0;
}

.side-menu:nth-child(5) i {
    color: #F44336 !important;
}

.side-menu:nth-child(5) a:hover {
    background: #f2f2f2;
    border-left: 5px solid #F44336;
}

.side-menu:nth-child(5) .active {
    border-left: 5px solid #F44336;
}

.side-menu:nth-child(6) i {
    color: #ff9800 !important;
}

.side-menu:nth-child(6) a:hover {
    background: #f2f2f2;
    border-left: 5px solid #ff9800;
}

.side-menu:nth-child(6) .active {
    border-left: 5px solid #ff9800;
}


.side-menu:nth-child(7) i {
    color: #795548 !important;
}

.side-menu:nth-child(7) a:hover {
    background: #f2f2f2;
    border-left: 5px solid #795548;
}

.side-menu:nth-child(7) .active {
    border-left: 5px solid #795548;
}

.side-menu span {
    font-size: 17px;
    vertical-align: middle;
    color: #888;
    margin-left: 15px;
}

.side-menu a:hover {
    background-color: #fff;
    -webkit-animation-name: example;
            animation-name: example;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
}






/* Standard syntax */
@-webkit-keyframes example {
    from {
        background-color: #fff;
        border-left-width: 1px;
    }

    to {
        background-color: #f2f2f2;
        border-left-width: 5px;
    }
}
@keyframes example {
    from {
        background-color: #fff;
        border-left-width: 1px;
    }

    to {
        background-color: #f2f2f2;
        border-left-width: 5px;
    }
}


