.front-page {
    /* background-image: url('../assets/home-bg.jpg'); */
    /* background-repeat: no-repeat; */
    /* background-size: cover; */
    background-color: #000;
    height: 100vh;
}

.font-page-layer {
    background: rgba(0, 0, 0, 0.5);
    height: 100%;
    position: relative;
}

.front-page .front-content {
    position: absolute;
    top: 34%;
    color: #fff;
    text-align: center;
    width: 100%;
}

.front-line-1 {
    font-size: 100px;
}

.front-line-1 span {
    color: #FF5722;
}

.front-line-2 {
    font-size: 30px;
}

.view-my-experience {
    border: 1px solid #f2f2f2;
