    font-size: 18px
}

.view-my-experience i {
    font-size: 30px;
    display: block;
    margin-top: 8px;
    font-weight: normal;
}

.view-my-experience:hover {
    background: #000;
    cursor: pointer;
    box-shadow: 5px 5px 10px #000;
}

.heading {
    background: #008073 !important;
    color: #fff;
    padding: 15px 25px;
    font-size: 40px;
    letter-spacing: 1px;
    font-family: 'Cinzel', serif;
}

.section-info {
    background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABoAAAAaCAAAAACMIRMSAAAAmElEQVR4AX2RwQ4EIQhD/f9ftYW7p6U0buYiEmLG5fGUXRlELda2eyEJ6mDFea36Kf8RZ1egUjFTOFTtn4RSFN6Uu7Pq0bu7iFx8U/ZEh3yyyEk1fFIy+CXiXB+Ve3TdGVxuN6XTeRo0Y6d38aMLH8ZvtBXjDK/BtayAreM0dBdXZqcsdq+cJv/9f/3l286UK68zZVKX6V0/HECKK51+49AAAAAASUVORK5CYII=);
    padding: 40px 50px;
    /* background: #f2f2f2; */
}

.contact-info {
    margin: 30px 0px 30px 20px;
}

.contact-info i {
    background: #000;
    color: #fff;
    width: 55px;
    vertical-align: middle;
    text-align: center;
    height: 55px;
    border-radius: 50%;
    line-height: 55px;
    box-shadow: 3px 4px 6px #000;
    font-size: 1.6rem;
}

.contact-info a {
    margin-left: 15px;
    font-size: 18px;
    vertical-align: middle;
    color: teal;
    font-family: 'Stylish', sans-serif;
}

.education-card,
.skills-card,
.experience-card {
    padding: 15px 20px;
    border: none;
    box-shadow: 2px 2px 6px grey;
}


.skills-card,
.experience-card {
    background: #fff;
}

.skills-card .skill {
    display: inline-block;
    width: 150px;
    text-align: center;
    margin-bottom: 25px;
}

.skill i,
.skill span {
    display: block;
}

.skill img {
    width: 60px;
}

.skill i {
    font-size: 60px;
}

.skill span {
    font-family: 'Stylish', sans-serif;
    color: #008073;
}

.college-name a {
    font-size: 25px;
    color: teal;
    line-height: 25px;
}

.education-city {
    color: gray;
    font-size: 14px;
}

.education-course {
    font-size: 20px;
    color: #FF5722;
    display: block;
}

.education-stream {
    font-size: 13px;
    color: #00BCD4;
    font-style: italic;
}

.experience-container {
    margin: 50px;
}

.experience-card {
    margin-bottom: 30px;
}

.experience-header {
    /* margin: 5px; */
    border-bottom: 1px solid #e2e2e2;
}

.experience-company {
    font-size: 25px;
}

.experience-position {
    font-size: 13px;
    color: teal;
    font-style: italic;
    margin-left: 10px;
    font-weight: 500;
    letter-spacing: 1px;
}

.project-card {
    background: #fff;
    box-shadow: 1px 3px 5px gray;
    margin-bottom: 30px;
    height: 430px;
}

.project-card img {
    width: 100%;
    height: 240px;
}

.project-details {
    padding: 15px;
}

.project-name a {
    font-size: 22px;
    color: #03A9F4;
    vertical-align: middle;
}


.project-desc {
    font-size: 12px;
    font-style: italic;
    color: #888;
}

.project-type {
    font-style: italic;
    background: #FF5722;
    padding: 2px 10px;
    color: #fff;
    border-radius: 10px;
    font-size: 10px;
    float: right;
    display: block;
    margin-top: 8px;
}

.project-technologies {
    color: teal;
    font-size: 13px;
    font-style: italic;
    font-weight: 500;
}

.project-role,
.project-members {
    font-size: 12px;
    color: #795548;
}

.project-members i,
.project-role i {
    margin-right: 5px;
}

.about-container {
    padding: 60px 55px;
}

.short-bio {
    border-left: 5px solid #795548;
    font-size: 28px;
    padding: 2px 20px;
    color: #795548;
    line-height: 30px;
    letter-spacing: 1px;
}

.about-myself {
    margin-top: 15px;
    font-size: 20px;
}

.current-focus {
    font-size: 20px;
}

.current-focus span {
    color: teal;
    margin-left: 10px;
}

.current-focus span:not(:last-child)::after {
    content: ' //';
    color: #000;
}

@media only screen and (max-width: 600px) {
    .left-side {
        display: none;
    }

    .front-content {
        top: 30%;
    }

    .front-line-1 {
        font-size: 70px;
    }

    .front-line-2 {
        font-size: 20px;
    }

    .about-container {
        padding: 20px;
    }

    .experience-container,
    .section-info {
        margin: 0px;
        margin-top: 10px;
        padding: 0px;
    }

    .skills-card {
        margin: 10px;
    }

    .education-card {
        margin-bottom: 15px;
    }
}

#about {
    scroll-behavior: smooth;
}

.footer-div {
    height: 45vh;
}
